
.dates {
    float: left;
    background-color: #ffffff;
    padding: 1rem;
    border-radius: 4px;
    color: #505e6c;
    box-shadow: 1px 1px 5px rgba(0,0,0,0.1);
}

.drop3 {
    min-width: 800px;
}

.date {
    display: flex;
    margin: 1rem;
    justify-content: center;
    padding: .2rem;
}

.box {
    display: flex;
    justify-content: space-evenly;
    width: 100%;
}

.head {
    text-align: center;
    padding: 1rem;
    color: grey;
}

.map {
    margin-left: 20px;
    min-width: 350px;
    width: 100%;
    height: 100%;
    border-radius: 4px;
    box-shadow: 1px 1px 5px rgba(0,0,0,0.1);
}

.select {
    display: flex;
    flex-direction: column;
}

.form1 {
    display: flex;
    color: #505e6c;
    padding: 1rem;
    text-align: center;
    align-content: space-between;
    justify-content: space-between;
}

.button{
    background: #ffed4f;
    border: none;
    border-radius: 4px;
    padding: 11px;
    box-shadow: none;
    text-shadow: none;
    outline: none !important;
    width: 200px;
}

.btn-primary:active {
    background: #ffed4f;
}

.btn-primary:active {
    transform: translateY(1px);
}
